.popup {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    align-items: center;
    display: flex;
    justify-content: center;
    background-color: rgba(0,0,0,.5);
    visibility: hidden;
    opacity: 0;
    transition: visibility .8s,opacity .3s linear;
}