.profile__name {
    margin: 0;
    padding: 0;
    max-width: 294px;
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    margin-right: 18px;
}

@media screen and (max-width: 610px){
    .profile__name {
        font-size: 27px;
        line-height: 33px;
        text-align: center;
        max-width: 189px;
        margin-right: 10px;
        padding-left: 27px;
    }
}